import React, {Component} from 'react';

export default class Navbar extends Component{
    constructor(props) {
        super(props);
        this.state = {
            detached: true
        }
    }

    handleScroll = () => {
        if (window.scrollY === 0) {
            this.setState(prev => ({detached: true}));
        } else {
            this.setState(prev => ({detached: false}));
        }
    }

    componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    render() {
        return (
        <nav class={this.state.detached && "nav-detached"}>
            <div class="logo">
                <img class="logo-img" src="images/logo-plutus-min.jpg"/>
            </div>
            <div class="navbar">
                <ul class="navbar-list">
                    <li class="navbar-list-item">ABOUT US</li>
                    <li class="navbar-list-item">OUR APPROACH</li>
                    <li class="navbar-list-item">OUR FOCUS</li>
                    <li class="navbar-list-item">OUR VALUE</li>
                    <li class="navbar-list-item">OUR TEAM</li>
                    <li class="navbar-list-item">OUR CONTACT</li>
                </ul>
            </div>
            {/* <div class="m-navbar">
                <ul class="m-navbar-list">
                    <li class="m-navbar-list-item">ABOUT US</li>
                    <li class="m-navbar-list-item">OUR APPROACH</li>
                    <li class="m-navbar-list-item">OUR FOCUS</li>
                    <li class="m-navbar-list-item">OUR VALUE</li>
                    <li class="m-navbar-list-item">OUR TEAM</li>
                    <li class="m-navbar-list-item">OUR CONTACT</li>
                </ul>
            </div> */}
        </nav>
        )
    }
}