import React, {Component} from 'react';
import './carousel.css';

const images = [
    'masthead-1.jpg',
    'masthead-2.jpg',
    'masthead-3.jpg'
];

const titles = [
    'WE KNOW WHAT IT TAKES TO ENHANCE ENTERPRISE VALUE',
    'TECHNOLOGY IS OUR SOLE FOCUS',
    'WE TRANSFORM BUSINESSES'
]

const subtitles = [
    "BEEN THERE. DONE THAT.",
    "IT'S IN OUR DNA",
    "IT'S OUR EDGE"
]

export default class Carousel extends Component {
    constructor(props) {
        super(props)
        this.state = {
            current_idx: 0
        }
    }

    changeImage(idx) {
        this.setState(prev => ({current_idx: idx}));
    }

    render() {
        const indicators = images.map(
            (val, idx) => <div  class="carousel-indicator" 
                                onClick={this.changeImage.bind(this, idx)}/>)

        return (
            <div class="carousel">

                <div class="carousel-img-cont">
                    <img class="carousel-img" src={`images/${images[this.state.current_idx]}`} alt="masthead"/>
                </div>
                <div class="carousel-box">
                    <h1 class="carousel-title">{titles[this.state.current_idx]}</h1>
                    <h3 class="carousel-subtitle">{subtitles[this.state.current_idx]}</h3>
                </div>
                <div class="carousel-indicators">
                    {indicators}
                </div>
                <div class="carousel-arrow"></div>
            </div>
        )
    }
}